<template>
  <div id="add-content">
    <a-form-model
        ref="ruleForm"
        :model="form"
        :rules="rules"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
    >
      <a-form-model-item label="记得是哪一天吗？" required prop="date1">
        <a-date-picker
            size="large"
            v-model="form.date1"
            type="date"
            placeholder="点击选择吧"
            style="width: 100%;"
            :default-value="moment()"
            format="YYYY年MM月DD号"
        />
      </a-form-model-item>
      <a-form-model-item ref="title" label="想一个标题吧" prop="title">
        <a-input
            v-model="form.title"
            size="large"
            @blur="
          () => {
            $refs.title.onFieldBlur();
          }
        "
        />
      </a-form-model-item>
      <a-form-model-item label="写点什么吧" prop="desc">
        <a-input v-model="form.desc" type="textarea" :auto-size="{ minRows: 6, maxRows: 12 }" size="large" />
      </a-form-model-item>
      <a-form-model-item label="加几张照片吧" prop="images">
        <a-upload
            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
            list-type="picture-card"
            :file-list="fileList"
            @preview="handlePreview"
            @change="handleChange"
        >
          <div v-if="fileList.length < 8">
            <a-icon type="plus" />
            <div class="ant-upload-text">
              点击添加图片
            </div>
          </div>
        </a-upload>
        <a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel">
          <img alt="example" style="width: 100%" :src="previewImage" />
        </a-modal>
      </a-form-model-item>
      <a-form-model-item :wrapper-col="{ span: 12, offset: 6 }">
        <a-button @click="resetForm">
          取消
        </a-button>
        <a-button type="primary" style="margin-left: 200px;" @click="onSubmit">
          发布
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>

<script>
import moment from 'moment';
export default {
  data() {
    return {
      labelCol: { span: 4 },
      wrapperCol: { span: 14 },
      other: '',
      form: {
        date1: undefined,
        title: '',
        desc: '',
        images: []
      },
      rules: {
        title: [
          { required: true, message: '这里还没有填呢', trigger: 'blur' },
          { min: 1, max: 10, message: '标题简单一点，不能超过10个字哦', trigger: 'blur' },
        ],
        date1: [{ required: true, message: '这里还没有填呢', trigger: 'change' }],
        desc: [{ required: true, message: '这里还没有填呢', trigger: 'blur' }],
      },
      previewVisible: false,
      previewImage: '',
      fileList: [
        {
          uid: '-1',
          name: 'image.png',
          status: 'done',
          url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
        },
        {
          uid: '-2',
          name: 'image.png',
          status: 'done',
          url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
        },
        {
          uid: '-3',
          name: 'image.png',
          status: 'done',
          url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
        },
        {
          uid: '-4',
          name: 'image.png',
          status: 'done',
          url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
        },
        {
          uid: '-5',
          name: 'image.png',
          status: 'error',
        },
      ],
    };
  },
  methods: {
    moment,
    onSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm() {
      this.$router.push("/")
    },
    handleCancel() {
      this.previewVisible = false;
    },
    async handlePreview(file) {
      if (!file.url && !file.preview) {
        file.preview = await getBase64(file.originFileObj);
      }
      this.previewImage = file.url || file.preview;
      this.previewVisible = true;
    },
    handleChange({ fileList }) {
      this.fileList = fileList;
    },
  },
}
</script>

<style scoped>
#add-content {
  margin: 50px 200px 0 200px;
}
</style>